.slider {
  max-width: 800px;
  margin: auto;
  position: relative;
  overflow: hidden;
}

.slides {
  transition: transform .5 ease;
  display: flex;
}

.slide {
  box-sizing: border-box;
  text-align: center;
  background: #f0f0f0;
  min-width: 100%;
  height: 400px;
  padding: 20px;
  display: none;
}

.slide.active {
  display: block;
}

button {
  color: #fff;
  cursor: pointer;
  background: #00000080;
  border: none;
  padding: 10px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.prev {
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

.next {
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.dots {
  text-align: center;
  margin-top: 10px;
}

.dot {
  cursor: pointer;
  background: #bbb;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  margin: 0 5px;
  display: inline-block;
}

.dot.active {
  background: #333;
}
